
.top{
  padding-bottom: 60px;
}
.featured {
    display: flex;
    flex-direction: column;
    gap: 16px;
    align-items: center;
    p.title {
      font-size: 18px;
      font-weight: bold;
    }
    .wrapper {
      .carousel-wrapper {
        display: flex;
        flex-direction: row;
        overflow-x: hidden;
        .scroll-x {
          display: flex;
          gap: 16px;
          .carousel {
            width: 350px;
            height: 150px;
            border-radius: 12px;
            position: relative;
            img.bg {
              border-radius: 12px;
              object-fit: cover;
            }
            .circle {
              display: flex;
              justify-content: center;
              align-items: flex-end;
              width: 64px;
              height: 64px;
              border-radius: 50%;
              left: 20px;
              position: absolute;
              top: -30px;
              background-color: #747474;
            }
            .text {
              left: 20px;
              position: absolute;
              top: 24px;
              color: #fff;
              font-weight: 600;
              font-size: 16px;
              line-height: 21px;
              span.gold {
                color: #f4d732;
              }
              span.grey {
                display: block;
                color: var(--secondary);
                font-size: 12px;
              }
            }
            .button {
              background-color: #d73624;
              position: absolute;
              bottom: 8px;
              left: 20px;
              border-radius: 4px;
              padding-left: 8px;
              display: flex;
              align-items: center;
              gap: 4px;
              span {
                color: #fff;
                font-weight: bold;
              }
              img-wrapper {
                width: 32px;
                height: 32px;
                background-color: #e64533;
                img {
                  object-fit: contain;
                }
              }
            }
          }
        }
      }
      .dots {
        display: flex;
        justify-content: center;
        gap: 8px;
        padding-top: 16px;
        span {
          width: 5px;
          height: 5px;
          border-radius: 50%;
          background-color: var(--secondary);
          display: block;
        }
        span:nth-child(1) {
          background-color: var(--primary);
        }
      }
    }
  }
  .alice-carousel__dots {
    margin: 0px 0px 0px;
    padding: 0;
    list-style: none;
    text-align: center;
}