.popular-food {
    display: flex;
    flex-direction: column;
    gap: 16px;
    p {
      font-size: 18px;
      font-weight: bold;
    }
    .foodlist {
      overflow: hidden;
      display: -webkit-box;
      gap: 12px;
      overflow: scroll;
      .foodItem {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        .imageWrapper {
          width: 64px;
          height: 64px;
          display: flex;
          justify-content: center;
          align-items: center;
          border: 1px solid #e2e2e2;
          border-radius: 12px;
        }
        p {
          font-size: 16px;
          font-weight: 400;
          padding-top: 8px;
        }
      }
    }
  }
  